export enum Api {
  EXPORT_EXCEL_URL = '/bi/personnelQualifications/qualificationsDetail/export-excel',
}

// 导出工单统计列表
export const exportExcel = (params) =>
  defHttp.download(
    {
      url: Api.EXPORT_EXCEL_URL,
      params,
    },
    { isReturnNativeResponse: true },
  );
